<template>
    <p :id="userID">Message : {{ inputValue }}</p>
    <input type="text" v-model="inputValue">

    <button v-bind:class="{ active: isActive }">Click me</button>
    <button @click="increase">Increase</button>
    <button @click="inputValue--">Decrease</button>


</template>

<script>
    export default {
        data() {
            return {
                inputValue: 20,
                isActive: true,
                userID: 100,
            }
        },

        methods: {
            increase() {
                this.inputValue++;
            }
        },

    computed() {
        return {
            computedValue: this.isActive ? 'active' : '',
        }
    }
    }
</script>


